@font-face {
  font-family: 'Open Sans';
  src: local('Open Sans Light'),
         local('OpenSans-Light'),
         url(iodide.dev.fonts/opensanslight.woff2) format('woff2'),
         url(iodide.dev.fonts/opensanslight.woff) format('woff'),
         url(iodide.dev.fonts/opensanslight.ttf) format('truetype');
  font-weight: 300;
  font-style: normal;
}

